/* Add this to your CSS file */
.buttons-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}
/* Modify your button CSS */
button.category-button {
  color: white;
  padding: 10px 20px;
  cursor: pointer;
  margin: 5px;
  width: 20%; /* Set button width to 100% of the container */
  border: none; /* Remove border */
  
}
