.starsall {
    background-color: rgb(172, 172, 181);
    display: flex;
    flex-direction: column;
    align-items: center; 
    justify-content: center;
    width: 100%;
    padding:10px 10px 10px;
}

.starsall form {
    display: flex;
    flex-direction: row; 
    width: 100%;
    color: black;
    align-items: center; 
    justify-content: center;
    padding: 10px 10px;
    align-items: center;
    
}

.starsall img {
    width: 40%;
    height: auto;
    margin-top: 10px; 
    padding:10px;
}

.starsall button {
    background-color:rgba(172, 172, 181, 0.265);
    justify-content: center; 
    align-items: center; 
    flex-wrap: nowrap;
    padding:10px;
    color:black;
    border-radius:1px;
    border-color: black;
}
.starsall select {
    background-color:rgba(172, 172, 181, 0.265);
    justify-content: center; 
    align-items: center; 
    flex-wrap: nowrap;
    padding:10px;
    color:black;
    border-radius:1px;
    border-color: black;
}
.starsall input {
    background-color:rgba(172, 172, 181, 0.265);
    justify-content: center; 
    align-items: center; 
    flex-wrap: nowrap;
    padding:10px;
    color:black;
    border-radius:1px;
    border-color: black;
}