.weather-container {
    text-align: center;
    padding-bottom: 10px;
    flex-direction: row;



}

.weather-container button {
    position: relative;
}

#weathermore {
    display: flex;
    flex-direction: row;
    background-color: rgba(172, 172, 181, 0.265);

    color: black;
    padding: 10px 10px;


}

#weathermore button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
}

#weathermore h2 {
    text-align: center;
}

.weatheritem {
    background-color: rgb(172, 172, 181);
    border: black;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 20px;
    margin: 1px;
    overflow: hidden;
    color: black;

    flex-direction: column;

}


.weatheritem button {
    color: black;
    position: relative;
    margin-top: auto;

}